<template>
  
  <div class="container">
    <h2 class="head">Budget Calculator</h2>
    <p class="total" :style="`${balance < 0 ? 'color: #d41010b4' : ''}`">
      {{ balance }}
    </p>
    <form class="form" @submit.prevent="submitForm">
      <input
        class="val"
        type="number"
        placeholder="Enter Income/Expense"
        v-model="budget.amount"
        required
      />
      <input
        class="des"
        type="text"
        placeholder="Description"
        v-model="budget.description"
        required
      />
      <button type="submit">Add</button>
    </form>
    <div class="all">
      <div class="incomes">
        <h2>Incomes</h2>
        <ul class="incomes" v-if="incomes.length > 0">
          <li v-for="(income, i) in incomes" :key="i">
            <div>
              <span class="description">{{ income.description }}</span>
              <span class="amount">{{ income.amount }}</span>
            </div>
            <span class="delete_btn" @click="dI(i)"><i class="fa fa-trash" aria-hidden="true"></i></span>
          </li>
        </ul>
        <div v-else>No Incomes Added yet.</div>
      </div>
      <div class="expenses">
        <h2>Expenses</h2>
        <ul class="expenses" v-if="expenses.length > 0">
          <li v-for="(expense, i) in expenses" :key="i">
            <div>
              <span class="description">{{ expense.description }}</span>
              <span class="amount" style="background: #ec1342b9">{{
                expense.amount
              }}</span>
            </div>
            
            <span class="delete_btn"  @click="de(i)"><i class="fa fa-trash" aria-hidden="true"></i></span>
          </li>
        </ul>
        <div v-else>No Expenses Added yet.</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      budget: {
        amount: null,
        description: "",
      },
      balance: 0,
      incomes: [],
      expenses: [],
    };
  },
  methods: {
    submitForm() {
      this.balance += parseInt(this.budget.amount);
      if (this.budget.amount >= 0) {
        this.incomes.push(this.budget);
      } else {
        this.expenses.push(this.budget);
      }
      this.budget = {
        amount: null,
        description: "",
      };
    },
    de(i){
      this.balance -= parseInt(this.expenses[i].amount);
      this.expenses.splice(i, 1);
    },
    dI(i){
      this.balance -= parseInt(this.incomes[i].amount);
      this.incomes.splice(i, 1);
    },
  },
};
</script>

